.navBar{
    width: 100%;
    height: 100%;   
    background: #1877F2;
    display: flex;
}
.navBar .logo   { 
 
    margin-left:5% ;
    margin-right: 30%;
}
.navBar .logo a{
    color: white;
    text-decoration: none;
}
.navBar .navItems ul{
    display: flex;
    padding: 4px;
    width: 25rem;
    justify-content: space-between;
}
.navBar .navItems ul li{
    list-style: none;
    font-size: 2rem;
    color: white;
    font-weight: 600;
}
.navBar .navItems ul li a{
    text-decoration: none;
    font-size: 2rem;
    color: white;
    font-weight: 600;
}
.navBar .loginButton{
    padding: 20px;
    margin-left: 10%;
}
.navBar .loginButton Button{
    background: #00C300;
    font-weight: 600;
    font-size: 1.1rem;
}